

// strat medicx project


.medicx-hero-section {
    margin-top: 144px;
    @include max(1199px) {
      margin-top: 0;
    }
}
.medicx-hero-item{
    width: 100%;
    z-index: 0;
    display: inline-block;
    padding: 150px 0;
    opacity: 1!important;
    position: relative;
    background-size: cover;
    @include max (1199px){
      padding: 300px 0 150px;
    }
    @include max (991px){
      padding: 220px 0 100px;
    }
    @include max (767px){
      padding: 150px 0 80px;
    }
    &::before{
      content: "";
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      position: absolute;
      background: linear-gradient(90deg, #030303 0%, rgba(3, 3, 3, 0) 68.5%);

    }
  }
.medicx-hero-slider{
  &-content {
    max-width: 900px;
    @include max(1199px) {
      margin: 0 auto;
      max-width: 800px;
      text-align: center;
    }
    p{
        font-size: 24px;
        line-height: 36px;
    }
    + .medicx-hero-btn-wrap{
      @include max(1199px) {
        justify-content: center;
      }
    }
  }
  &-content2{
    text-align: center;
    max-width: 1100px;
    margin: 0 auto;
    h6{
      margin-bottom: 20px;
    }
    p{
      font-size: 24px;
      line-height: 36px;
    }
  }
}
.medicx-hero-slider-init .slick-slide {
    -webkit-backface-visibility: hidden;
    -webkit-transform: translate3d(0, 0, 0);
}

.medicx-hero-slider-init2 ul.slick-dots {
  position: absolute;
  bottom: 50%;
  right: 10%;
  li{
    margin-bottom: 24px;
  }
}
// .medicx-hero-slider-init2 .slick-dots li.slick-active button {
//   width: 30px;
