// }
.slick-dots button {
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 100px;
  padding: 0;
  border: none;
  background-color: var(--white-color);
  text-indent: -9999px;
  transition: .3s;
}
.medicx-left-arrowbtn {
    position: absolute;
    top: 55%;
    left: 5%;
    width: 50px;
    height: 50px;
    background: white;
    border-radius: 100px;
    img{
        position: absolute;
        top: 22%;
        right: 40%;
        width: 15px;
        cursor: pointer;
    }
}
.medicx-right-arrowbtn {
    position: absolute;
    top: 55%;
    right: 5%;
    width: 50px;
    height: 50px;
    background: white;
    border-radius: 100px;
    img{
        position: absolute;
        top: 24%;
        right: 30%;
        width: 15px;
        cursor: pointer;
    }
}



.medicx-hero-slider-init .medicx-hero-next, 
.medicx-hero-slider-init .medicx-hero-prev {
  top: 50%;
  left: 50px;
  width: 80px;
  height: 80px;
  z-index: 99;
  display: flex;
  align-items: center;
  cursor: pointer;
  position: absolute;
  background-size: 20px;
  background-position: center;
  background-repeat: no-repeat;
  transition: all .4s;
  justify-content: center;
  background-image: url(../images/v1/leftarrow.svg);
  border: none;
  background-color: var(--white-color);
  border-radius: 100px;
  transform: translateY(-50%);

  @include max (1450px){
    width: 65px;
    height: 65px;
    left: 20px;
    background-size: 15px;
  }
  @include max (767px){
    display: none!important;
  }
}
.medicx-hero-slider-init .medicx-hero-prev {
  left: auto;
  right: 50px;
  background-image: url(../images/v1/rightarrow.svg);
  @include max (1450px){
    right: 20px;
  }

  @include max (767px){
    display: none!important;
  }
}

.slider-custom-anim-left.slider-animated {
    --animation-name: custom-anim-left;
  }
.slider-custom-anim-left {
  opacity: 0;
  animation-fill-mode: both;
  animation-iteration-count: 1;
  animation-duration: 1s;
  animation-delay: 0.3s;
  animation-name: var(--animation-name);
}

.slider-animated {
    opacity: 1;
  }

  .custom-anim-left {
    animation: custom-anim-left 1.3s forwards cubic-bezier(0.645, 0.045, 0.355, 1) 0.4s;
    opacity: 0;
  }
  
  @keyframes custom-anim-left {
    0% {
      transform: translateX(-5%);
      clip-path: inset(0 100% 0 0);
      opacity: 0;
    }
    100% {
      transform: translateX(0);
      clip-path: inset(0 0 0 0);
      opacity: 1;
    }
  }


  [data-animation-in] {
    opacity: 0;
    -webkit-animation-duration: 2s;
    animation-duration: 2s;
    -webkit-transition: opacity 0.5s ease 0.3s;
    transition: opacity 0.5s ease 0.3s;
  }
  .medicx-hero-slider-content .animated {
    transition: all 0.5s ease;
    opacity: 1;
  }

  .medicx-divider{
    height: 1px;
    width: 100%;
    background-color: var(--light-color2);
  }