// fonts variable
$body-font: 'Plus Jakarta Sans', sans-serif!default;
$heading-font: 'Plus Jakarta Sans', sans-serif !default;


// color variable
:root {
  --heading-color: #030303;
  --body-color: #333333;
  --accent-color: #0022FF;
  --white-color: #fff;
  --light-color: #CBD2D9;
  --light-color2: #EAEDF0;
  --white-bg: #fff;
}
$colors: (
 
);

$bodyfontScale: (
  xl: (
    20: 20px,
    lineheight: 32px,
  ),
  lg: (
    18: 18px,
    lineheight: 28px,
  ),
  md: (
    16: 16px,
    lineheight: 24px,
  ),
  xs: (
    14: 14px,
    lineheight: 22px,
  ),
  xss: (
    12: 12px,
    lineheight: 20px,
  ),
);

// button font size
$button_font_size: 16px;
$buton_line_height: 28px;
$button_padding: 18px 34px;
$button_font_weight: 700;
$button_font_family: $body-font;

