.medicx-team-member{
    &-wrap{
        position: relative;
        display: block;
        border-radius: 17px;
        transition: all 0.4s;
        box-shadow: 0px 4px 80px 0px #00000012;
        border: 1px solid var(--light-color);
        margin-bottom: 24px;
        &:hover{
            border: 1px solid var(--white-color);
        }
        &:hover .medicx-team-member-img::before{
            opacity: 1;
        }
        &:hover .social_position{
            opacity: 1;
            visibility: visible;
            bottom: 30px;
        }
    }
    &-img {
        position: relative;
        &::before{
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background: linear-gradient(180deg, rgba(3, 3, 3, 0) 32.5%, rgba(3, 3, 3, 0.8) 100%);
            opacity: 0;
            transition: .3s;
        }
        img{
            width: 100%;
        }
      
    }
    &-content{
        padding: 30px;
        h5{
            margin-bottom: 5px;
        }
    }
}


.medicx-team-member-wrap:hover .medicx-team-member-social-icon {
    opacity: 1;
}

.medicx-team-member-social-icon {
    position: absolute;
    transform: translate(-50%, -50%);
    top: 60%;
    left: 50%;
    gap: 24px;
    display: flex;
    justify-content: center;
    opacity: 0;
    transition: .3s;
}


