header.medicx-header-section {
  position: fixed;
  z-index: 9;
  width: 100%;
  top: 0;
  transition: all 0.4s;
  background: var(--white-color);
}
header.medicx-header-section.sticky-menu{
  box-shadow: 0 4px 80px rgb(0 0 0 / 6%);
  background-color: var(--white-bg);
  margin-top: -60px;
  @include max (1199px){
    margin-top: -105px;
  }
  @include max (991px){
    margin-top: 0;
  }
  .sticky-menu2{
    margin-top: -85px!important;
  }
}
header.medicx-header-section.sticky-menu2{
  box-shadow: 0 4px 80px rgb(0 0 0 / 6%);
  background-color: var(--white-bg);
  margin-top: -85px;
  @include max (1199px){
    margin-top: -105px;
  }
  @include max (991px){
    margin-top: 0;
  }
  
}


.white-color{
  color: var(--white-color)!important;
}
header.medicx-header-section.sticky-menu.inner-header{

  background-color: var(--dark-bg)!important;
}
.inner-header .site-menu-main .nav-link-item {
  color: var(--white-color)!important;
  @include max(991px){
    color: var(--dark-bg)!important;
  }
}
// v9
.header-three-site-btn{
  align-items: center;
  gap: 12px;
  a.header-three-btn{
    color: var(--heading-color);
    font-size: 16px;
    line-height: 24px;
    font-weight: 700;
  }
}




// medicx project

.medicx-header{
  &-top{
    background: var(--accent-color);
    padding: 15px 0;
    @include max (991px){
      display: none;
    }
  }
  &-top2{
    background: var(--white-color);
    padding: 15px 0;
  }
}
.medicx-header{
  &-bottom {
    padding: 15px 0;
    background: var(--white-color);
  }
  &-bottom2 {
    padding: 15px 0;
    background: var(--accent-color);
  }
}
.medicx-header-info{
  &-wrap{
    display: flex;
    justify-content: space-between;
    @include max (1199px){
      display: block;
      text-align: center;
    }
  }
  &-wrap2{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &-data{
    ul{
      li{
        display: inline-block;
        margin-right: 20px;
        color: var(--white-color);
        img{
          margin-right: 10px;
        }
        @include max (767px){
          margin-bottom: 10px;
        }
        a{
          margin-right: 20px;
          color: var(--white-color);
         
          img{
            margin-right: 10px;
          }
        }
      }
    }
  }
  
}

.medicx-social-icon{
  &-wrap{
    @include max (1199px){
      margin-top: 15px;
    }
  }
  &-img{
      ul {
          li{
              display: inline-block;
              @include max (767px){
                margin-bottom: 20px;
              }
              a{
                margin-right: 30px;
                
              }
          }
      }
  }
}



