


.medicx-iconbox{
    &-wrap {
        border-radius: 10px;
        background-color: var(--white-color);
        padding: 30px;
        margin-bottom: 24px;
        transition: all 0.4s;
        &:hover{
            background: var(--accent-color);
            .medicx-iconbox-data{
                h4,p{
                    color: var(--white-color);
                }
            }
            .medicx-iconbox-btn{
                    color: var(--white-color);
                    svg path{
                        stroke: var(--white-color);
                    }
            }
            
            .medicx-iconbox-icon{
                background: var(--white-color);
            }
            svg path{
                fill: var(--accent-color);
            }
            svg.icon-stroke path{
                stroke: var(--accent-color);
                fill: #fff;
                opacity: 1;
            }
           
        }
    }
    &-icon {
        margin-bottom: 24px;
        width: 80px;
        height: 80px;
        display: flex;
        align-items: center;
        border-radius: 100px;
        justify-content: center;
        background: var(--accent-color);
        position: relative;
        transition: all .4s;
    }
    &-data {
        h4 {
            font-size: 34px;
            transition: all 0.4s;
            margin-bottom: 15px;
        }
        p{
            transition: all 0.4s;

        }
        
       
    }
    &-btn{
        color: var(--accent-color);
        font-weight: 700;
        svg{
            transition: all .4s;
            margin-left: 10px;
            path{
                transition: all 0.4s;
            }
        }
        
    }
    &-wrap2{
        margin-bottom: 24px;
        &:last-child{
            margin-bottom: 0;
        }
    }
    &-data2{
        h4{
            margin-bottom: 15px;
        }
    }
}

