.medicx-social-icon{
    &.social_position{
        transition: all .4s;
        position: absolute;
        bottom: 0;
        text-align: center;
        width: 100%;
        opacity: 0;
        visibility: hidden;
        
    }
    ul{
        li{
            display: inline-block;
            margin-right: 24px;
            &:last-child{
                margin-right: 0;
            }
            a{
                width: 50px;
                height: 50px;
                border-radius: 100px;
                border: 1px solid var(--white-color);
                display: flex;
                align-items: center;
                justify-content: center;
                transition: all 0.4s;
                &:hover{
                    background: var(--accent-color);
                    border: transparent;
                }
                img{
                    width: auto;
                }
            }
        }
    }
    &-wrap2{
        display: flex;
        gap: 15px;
        align-items: center;
    }
    &-data2{
        p{
            font-size: 16px;
            margin-bottom: 0!important;
        }
    }
    &-wrapper {
        display: flex;
        gap: 40px;
        @include max (1199px){
            gap: 12px;
        }
    }
}

