.location-icon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.contactus-authore{
    &-wrapper{
        margin-top: 50px;
        @include max (991px){
            margin-top: 0;
            margin-bottom: 40px;
        }
        @include max (425px){
            margin-bottom: 20px;
        }
    }
}
.responsive-map{
    overflow: hidden;
    position:relative;
    height: 600px;
    @include max (991px){
        height: 500px;
    }
    @include max (767px){
        height: 400px;
    }
}
.responsive-map iframe{
    left:0;
    top:0;
    height:100%;
    width:100%;
    position:absolute;
}






.medicx-form-box{
    input{
        padding: 0;
        height: initial;
        width: initial;
        margin-bottom: 0;
        display: none;
        cursor: pointer;
    }
    label{
    position: relative;
    cursor: pointer;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--dark-bg);
    @include max(767px){
        display: block;
    }
        &::before{
            content: "";
            -webkit-appearance: none;
            background-color: transparent;
            border: 2px solid var(--body-color);
            padding: 7px;
            display: inline-block;
            position: relative;
            vertical-align: middle;
            cursor: pointer;
            margin-right: 12px;
            margin-top: -2px;
        } 
    }
}
.medicx-form-box input:checked+label:after {
    content: "";
    display: block;
    position: absolute;
    top: 7px;
    left: 6px;
    width: 5px;
    height: 9px;
    border: solid var(--dark-bg);
    border-width: 0 2px 2px 0;
    transform: rotate(45deg);
}
button#medicx-submit-btn{
    border-radius: 50px;
    &:hover{
        color: var(--white-color)!important;
        background-color: var(--dark-bg);
    }
}
.reset-pswrd{
    margin: 24px 0 0 0!important;
}

.medicx-post-comment-thumb{
    img{
        width: 100%;
    }
}
// medicx project
.medicx-contact-schedule{
    &-time-wrap {
        padding: 24px;
        background: var(--accent-color);
        border-radius: 10px;
    }
    &-title {
        text-align: center;
        margin-bottom: 30px;
    }
    &-time {
        display: flex;
        justify-content: space-between;
        @include max (991px){
            justify-content: center;
            gap: 180px;
        }
        @include max (991px){
            justify-content: center;
            gap: 110px;
        }
        .date{
            h6{
                margin-bottom: 24px;
                &:last-child{
                    margin-bottom: 0;
                }
            }
        }
        .time{
            p{
                margin-bottom: 18px;
                &:last-child{
                    margin-bottom: 0;
                }
            }
        }
    }
}
